.btn-explore button {
  --border-radius: 15px;
  --border-width: 4px;
  -webkit-appearance: none;
          appearance: none;
  position: relative;
  padding: 1em 4rem;
  border: 0;
  background-color: #212121;
  font-family: "Roboto", Arial, "Segoe UI", sans-serif;
  font-size: 18px;
  font-weight: 500;
  color: #fff;
  z-index: 2;
  border-radius: 15px;
}
.btn-explore button::after {
  --m-i: linear-gradient(#61dafb, #ff6c6c);
  --m-o: content-box, padding-box;
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  padding: var(--border-width);
  border-radius: var(--border-radius);
  background-image: linear-gradient(#00c8ff, #cf1a6f);

  -webkit-mask-image: var(--m-i), var(--m-i);
  mask-image: var(--m-i), var(--m-i);
  -webkit-mask-origin: var(--m-o);
  mask-origin: var(--m-o);
  -webkit-mask-clip: var(--m-o);
  mask-composite: exclude;
  -webkit-mask-composite: destination-out;
  -webkit-filter: hue-rotate(0);
          filter: hue-rotate(0);
  -webkit-animation: rotate-hue linear 3000ms infinite;
          animation: rotate-hue linear 3000ms infinite;
  -webkit-animation-play-state: paused;
          animation-play-state: paused;
}
.btn-explore button:hover::after {
  -webkit-animation-play-state: running;
          animation-play-state: running;
}

@-webkit-keyframes rotate-hue {
  to {
    -webkit-filter: hue-rotate(1turn);
            filter: hue-rotate(1turn);
  }
}

@keyframes rotate-hue {
  to {
    -webkit-filter: hue-rotate(1turn);
            filter: hue-rotate(1turn);
  }
}

.btn-explore button,
.btn-explore button::after {
  box-sizing: border-box;
}

.btn-explore button:active {
  --border-width: 5px;
}
